@include new("team") {

    @include when("full") {
        background-color: $white;

        .team__member {
            height: 40rem;
        }
    }

    @include when("contact") {
        background-color: rgba($black, 0.1);

        .team__member {
            height: 30rem;
        }
    }

    //
    // GENERAL TYPO SETTINGS
    // --------------------------------------------------------------------------

    h3 {
        margin-bottom: 1.4rem;
        @include fontSet(16px, 18px);
        font-weight: bold;
        text-transform: uppercase;
        color: $main-color;
    }

    h4 {
        @include fontSet(24px, 26px);
        font-weight: normal;
        color: $white;
    }

    p {
        @include fontSet(20px, 20px);
        color: $white;
    }


    //
    // BOXES
    // --------------------------------------------------------------------------

    @include has("wrapper") {
        display: flex;
        height: 100%;
    }

    @include has("info") {
        position: relative;
        flex-basis: 20%;
        max-width: 20%;
        margin-right: 0.5rem;
        padding: 1rem;
        background-color: $complementary-color;

        .viewmore__arrow__button {
            @include arrow-link($main-color);
            @include fontSet(20px, 20px);
            position: absolute;
            bottom: 20px;

            &:before,
            &:after {
                top: -3px;
            }
        }
    }

    @include has("list") {
        flex-basis: 80%;
        max-width: 80%;
        display: flex;
        flex-flow: row nowrap;
    }

    @include has("member") {
        flex: 1;
        height: 100%;
        height: auto;
        margin-right: 0.5rem;
        padding: 1rem;
        position: relative;
        overflow: hidden;
        display: flex;
        flex-flow: column nowrap;
        // background-size: cover;
        // background-position: 0 30%;
        background-size: auto 100%;
        background-position: 92% 0;
        background-repeat: no-repeat;
        // background-blend-mode: normal;
        background-color: rgba($black, 0.5);
        // background-image: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(255,255,255,1) 100%);
        transition: all 0.4s ease-in-out;

        &:last-child {
            margin-right: 0;
        }

        @include has("link") {
            @include box-hidden-link;
        }

        @include has("position") {}
        @include has("name") {}

        @include has("resume") {
            position: absolute;
            bottom: 1rem;
            height: 60px;
            overflow: hidden;
            opacity: 0;
            transition: opacity 0.8s ease-out;

            p {
                margin-bottom: 0;
            }
        }

        &:only-child {
            background-position: 100% 100%;
            background-size: auto 130%;

            .team__member__resume {
                width: 40%;
                opacity: 1;
            }
        }


        @include on-event() {
            // flex-grow: 3;
            // background-position: 92% 0;
            // background-blend-mode: overlay;

            flex-grow: 10;
            background-position: 94% 0;
            background-blend-mode: luminosity;
            background-color: rgba($complementary-color, 0.3);

            .team__member__resume {
                width: 50%;
                opacity: 1;
            }

            &:only-child {
                background-position: 100% 100%;
                background-size: auto 130%;

                .team__member__resume {
                    width: 40%;
                }
            }
        }

        //
        // maybe... ¿?
        //
        // img {
        //     width: 100%;
        //     height: 100%;
        //     object-fit: cover;
        //     filter: grayscale(100%);
        //     transition: all 1s ease-in-out;
        // }


    }

}
